var userCenter = {
	init: function(){
		console.log('');
	},
    
    //
	signOut: function(){

		//window.location.href = ""
        utils.routerLoad( com.root + "../login.html", true);//
	},
     
    //
	modifyPwd: function(){
        var constraints = {
	        oldPassword: {
	            presence: true
	        },
	        ePassword1: {
	            presence: true,
	            length: {
	                minimum: 6,
	                maximum: 20
	            }
	        },
	        ePassword2: {
	            presence: true,
	            equality: {
	                attribute: "ePassword1",
	                message: "^2"
	            }
	        }

	    }
        
        if( utils.validate('#editPwd', constraints) ){
           var formValues = utils.collectFormValues('#editPwd');
	       console.log(formValues);
	       utils.routerLoad(com.root + "../login.html", true);
	    }

	}
};