var email = {
	domTree: null,   //
	init: function(){
         email.addressList();		
	},
    // 
    addressList: function() {
        if(!email.domTree) {
             email.domTree = tree({
				target: "#listAddress",   // id
				// 
	            data: [{
	                id: '1',
	                item: [{
	                    face:"/cos/styles/mobile/images/user_male.png",
	                    id:10000003730292,
	                    isCharge:1,
	                    name:"()",
	                    online:1,
	                    orgpathname:"/",
	                    sex:"1"
	                },{
	                    face:"/cos/styles/mobile/images/user_male.png",
	                    id:10000003730293,
	                    isCharge:1,
	                    name:"(2)",
	                    online:1,
	                    orgpathname:"/",
	                    sex:"1"
	                }],
	                name: "",
	                nums: 2
	            },{
	                id: '1',
	                item: [],
	                name: "/",
	                nums: 2
	            }],
	            id: "id",          //
	            checkbox: true,    //  false
	            name: "name",      //
	            children: "item",  //
	            list: {            //
	              id: "id",
	              name: "",
	              online: "",
	              orgpathname: ""
	           }
			});
        }

    },
    //
    ok: function() {
    	var value = "",  
    	    reVal = email.domTree.getCheckedData(), //
    	    n;

    	for( n in reVal) {
            value += reVal[n].name + ",";
    	}

    	value = value.substring(0, value.length);    	

    	$("#sender").val(value);

    	utils.routerBack();
    	//utils.routerLoad("#email");
    }

};